.auto-rs-power {
  background: #d7e5f01f;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 16px;
  border-radius: 16px;
}

.auto-rs-power p {
  margin: 0;
}

.auto-rs-power div p:first-child {
  font-weight: 700;
  margin-bottom: 4px;
}

.auto-rs-power div p:last-child {
  font-size: 12px;
  color: #b1b1b2;
}

.vip-button {
  width: 80%;
  border: 0;
  background: #2a3038;
  padding: 12px 8px;
  border-radius: 100px;
  font-weight: 700;
  color: #ffffff;
  cursor: "pointer";
}

.button-wrapper {
  display: flex;
  flex-direction: column;
  gap: 2px;
}
